<script>
import {
  fetchGetUldlthp,
} from "@/api/demo/index";
import instenceSelect from '../compoents/instenceSelect.vue';
export default {
  components: { instenceSelect },
  name: "performUldlthp",
  data() {
    return {
      instanceValue: "", //当前选中的实例
      isFirst: true,
      loading: false,
      serverData: {},
    };
  },
  methods: {
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.serverData = {};
      this.instanceValue = val

      // 获取告警数据
      this.getData();
    },
    // 获取数据
    getData() {
      this.loading = true;
      fetchGetUldlthp({
        request_type: "traffic load",
        instance_id: this.instanceValue,
        sequence_no: 3358,
      })
        .then((response) => {
          this.loading = false;
          console.log(response);
          if ((response != null) & (response != [])) {
            this.serverData = response;
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>

